.appWrap {
  box-sizing: border-box;
  min-height: 100vh;
  padding: 40px 200px; 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  @media screen and (max-width: 1100px) {
    padding: 40px 50px;
  }
  @media screen and (max-width: 800px) {
    padding: 20px 20px;
  }
  @media screen and (max-width: 450px) {
    padding: 2%;
  }
}
